body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
  font-family: 'Bungee', cursive;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}
.card {
  border: 4px solid;
  border-radius: 2px;
  width: 160px;
  height: 160px;
  margin: 1rem;
  position: relative;
  box-shadow: 2px 2px 5px rgb(255, 249, 198);
  transition: all .2s ease-in-out;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  background:black;
}

.card > .img-container > img {
  height: 100%;
}

.card:hover {
  box-shadow: 8px 8px 20px rgb(252, 227, 3);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

.card:active {
  -webkit-transform: translate(4px, 15px);
          transform: translate(4px, 15px);
}
nav {
    width: 100%;
    font-size: 1.5rem;
    color: #fff;
    margin-bottom: 20px;
    background-color:  rgba(253, 173, 1, 0.788);
    height: auto;
    font-family: 'Sanchez', serif, cursive;
  }
  
  a {
    color: #070000;
    font-size: 2.6rem;
  }
  
  a:hover {
    color: black;
    text-decoration: none;
  }
  
  nav li {
    display: inline-block;
    padding: 10px 0;
    width: 33.33%;
    text-align: center;
  }
  
  ul {
    padding-left: 0px;
  }

  .alignLeft{
    text-align: left;
    padding-left: 1%;
  }

  .alignRight{
    color: black;
    text-align: right;
    padding-right: 1%;
    font-size: 2.2rem;
  }
