.card {
  border: 4px solid;
  border-radius: 2px;
  width: 160px;
  height: 160px;
  margin: 1rem;
  position: relative;
  box-shadow: 2px 2px 5px rgb(255, 249, 198);
  transition: all .2s ease-in-out;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  background:black;
}

.card > .img-container > img {
  height: 100%;
}

.card:hover {
  box-shadow: 8px 8px 20px rgb(252, 227, 3);
  transform: scale(1.1);
  cursor: pointer;
}

.card:active {
  transform: translate(4px, 15px);
}